require('../sass/detail.scss');
(function () {
    var newsData = [],
            newsId = parseInt(getQueryString('id'))-1;
    $.ajax({
        type: 'GET',
        url: 'noMD5/news.json',
        data: {},
        dataType: 'json',
        timeout: 2000,
        success: function (data, status, xhr) {
            vm.newsData = data[newsId];
        },
        error: function (xhr, type, error) {
        }
    });
    $('.img-wrap img').attr('src','noMD5/news-b'+(newsId+1)+'.jpg');
    var vm = new Vue({
        el: '#main',
        data: {
            newsData: newsData
        },
        components: {
            'c-footer': require('../components/c-footer'),
            'c-header': require('../components/c-header')
        },
        ready:function(){
        }
    });
    function getQueryString(name) {
        var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
        var r = window.location.search.substr(1).match(reg);
        if (r != null) {
            return r[2];
        }
        return '';
    }
})();


